<template>
  <div class="banner-box" ref="root">
    <!-- 轮播图区域 -->
    <div class="wrapper">
      <div
        v-for="item in state.source"
        :key="item.id"
        :class="item.className"
        :style="item.sty"
      >
        <img :src="item.pic" alt="" />
        <div class="mark"></div>
        <p class="detail">
          <slot :item="item"></slot>
        </p>
      </div>
    </div>
    <!-- 左右切换按钮 -->
    <a
      href="javascript:;"
      class="arrow arrow-left"
      v-throttle="[change.bind(null, 'left'), 500]"
    ></a>
    <a
      href="javascript:;"
      class="arrow arrow-right"
      v-throttle="[change.bind(null, 'right'), 500]"
    ></a>
  </div>
</template>

<script>
import { ref, reactive, onMounted, watch } from "vue";

export default {
  // 处理属性规则
  props: {
    source: {
      required: true,
      type: Array,
    },
    initial: {
      default: 0,
      type: Number,
    },
    interval: {
      default: 3000,
      type: Number,
    },
  },
  // 入口
  setup(props, context) {
    /* 处理source */
    const computed = (step, source) => {
      // 计算五张图片的索引
      let len = source.length,
        temp1 = step - 2,
        temp2 = step - 1,
        temp3 = step,
        temp4 = step + 1,
        temp5 = step + 2;
      temp1 = temp1 < 0 ? len + temp1 : temp1;
      temp2 = temp2 < 0 ? len + temp2 : temp2;
      temp4 = temp4 >= len ? temp4 - len : temp4;
      temp5 = temp5 >= len ? temp5 - len : temp5;
      // 计算每一条数据的样式
      return source.map((item, index) => {
        let transform = `translate(-50%, -50%) scale(0.55)`,
          zIndex = 0,
          className = "slide";
        switch (index) {
          case temp3:
            transform = `scale(1) translate(-50%, -50%)`;
            zIndex = 3;
            className = "slide active";
            break;
          case temp2:
            transform = `translate(-130%, -50%) scale(.85)`;
            zIndex = 2;
            break;
          case temp4:
            transform = `translate(30%, -50%) scale(.85)`;
            zIndex = 2;
            break;
          case temp1:
            transform = `translate(-195%, -50%) scale(.7)`;
            zIndex = 1;
            break;
          case temp5:
            transform = `translate(95%, -50%) scale(.7)`;
            zIndex = 1;
            break;
        }
        item.sty = {
          transform,
          zIndex,
        };
        item.className = className;
        return item;
      });
    };
    // 不足六个补充为六个
    let source = props.source,
      diff = source.length - 6;
    if (diff < 0) {
      diff = Math.abs(diff);
      source.slice(0, diff).forEach((item) => {
        source.push({
          ...item,
          id: parseInt(source[source.length - 1].id) + 1,
        });
      });
    }
    // 初始带样式的数据
    source = computed(props.initial, source);

    /* 把传递的属性值赋值给响应式状态 */
    let state = reactive({
      ...props,
      source,
    });
    watch(
      () => state.initial,
      (initial, preInitial) => {
        state.source = computed(initial, state.source);
      }
    );

    /* 获取容器的DOM元素 */
    let root = ref(null);

    /* 每间隔NMS自动轮播一次 */
    let timer = null;
    const autoPlay = () => {
      // 自动轮播
      timer = setInterval(() => {
        state.initial++;
        if (state.initial >= state.source.length) {
          state.initial = 0;
        }
      }, state.interval);
    };
    onMounted(() => {
      autoPlay();
      root.value.onmouseenter = () => {
        clearInterval(timer);
      };
      root.value.onmouseleave = () => {
        autoPlay();
      };
    });

    /* 左右切换 */
    const change = (dir) => {
      if (dir === "right") {
        state.initial++;
        if (state.initial >= state.source.length) {
          state.initial = 0;
        }
        return;
      }
      state.initial--;
      if (state.initial < 0) {
        state.initial = state.source.length - 1;
      }
    };

    return {
      state,
      root,
      change,
    };
  },
};
</script>

<style lang="less" scoped>
.banner-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;

  .wrapper {
    position: relative;
    box-sizing: border-box;
    height: 100%;

    .slide {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 0;
      transform: translate(-50%, -50%);
      box-sizing: border-box;
      width: 25%;
      height: 100%;
      border: 3px solid #000;
      overflow: hidden;
      transition: 0.5s;

      img {
        display: block;
        width: 100%;
        height: 100%;
      }

      .mark {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.75);
        transition: 0.3s;
      }

      .detail {
        position: absolute;
        left: 0;
        bottom: 0;
        box-sizing: border-box;
        padding: 10px;
        width: 100%;
        height: 30%;
        background: rgba(0, 0, 0, 0.75);
        background: -webkit-linear-gradient(
          top,
          rgba(0, 0, 0, 0.35),
          rgba(0, 0, 0, 0.75)
        );
        transform: translateY(100%);
        transition: 0.3s;

        span {
          display: block;
          line-height: 2;
          font-size: 14px;
          color: #fff;
        }
      }

      &.active .mark,
      &:hover .mark {
        background: rgba(0, 0, 0, 0);
      }

      &.active:hover .detail {
        transform: translateY(0);
      }
    }
  }

  .arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 39px;
    height: 80px;
    background: url("../assets/images/btn.png") no-repeat center center;

    &.arrow-left {
      left: 0;
      background-position: 0 0;
    }

    &.arrow-right {
      right: 0;
      background-position: -39px 0;
    }
  }
}
</style>